// ** React Imports
import { useState } from 'react'

import { useForm } from 'react-hook-form'
import { selectThemeColors, getAuthToken } from '@utils'
import axios from 'axios'
// ** Third Party Components
import Flatpickr from 'react-flatpickr'
import { User, Briefcase, Mail, Calendar, DollarSign, X } from 'react-feather'
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  FormGroup,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Input,
  Label,
  Form
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/flatpickr/flatpickr.scss'

const AddNewModal = ({ open, handleModal }) => {
  // ** State
  const [Picker, setPicker] = useState(new Date())

  const { register, errors, handleSubmit } = useForm()
  // ** Custom close btn
  const CloseBtn = <X className='cursor-pointer' size={15} onClick={handleModal} />

  const onSubmit = (data) => {
    console.log(data)
    axios.post('http://68.183.222.133:5000/products/new', data,
  {
    headers: {
      Authorization : JSON.parse(getAuthToken())
    }
  }
  ).then(response => {
   // console.log('---ppp--')
   // console.log(response.data)
    handleModal()
   // data = response.data
   
  })
  .catch(err => console.log(err))
  }
  return (
    <Modal
      isOpen={open}
      toggle={handleModal}
      className='sidebar-sm'
      modalClassName='modal-slide-in'
      contentClassName='pt-0'
    >
      <ModalHeader className='mb-3' toggle={handleModal} close={CloseBtn} tag='div'>
        <h5 className='modal-title'>Nouveau Produit</h5>
      </ModalHeader>
      <ModalBody className='flex-grow-1'>
        
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label for='label'>Produit</Label>
          <InputGroup>
            <InputGroupAddon addonType='prepend'>
              <InputGroupText>
                <Briefcase size={15} />
              </InputGroupText>
            </InputGroupAddon>
            <Input id='label' placeholder='Produit' required name='label' 
            innerRef={register({ required: true, validate: value => value !== '' })}/>
          </InputGroup>
        </FormGroup>
        
        <FormGroup>
          <Label for='descp'>Description</Label>
          <InputGroup>
            <InputGroupAddon addonType='prepend'>
              <InputGroupText>
                <Briefcase size={15} />
              </InputGroupText>
            </InputGroupAddon>
            <Input id='descp' placeholder='Description' required name='descp' 
            innerRef={register({ required: true, validate: value => value !== '' })}/>
          </InputGroup>
        </FormGroup>

        <FormGroup className='mb-4'>
          <Label for='price' >Prix</Label>
          <InputGroup>
            <InputGroupAddon addonType='prepend'>
              <InputGroupText>
                <DollarSign size={15} />
              </InputGroupText>
            </InputGroupAddon>
            <Input type='number' id='price' placeholder='Prix' required name='price' 
            innerRef={register({ required: true, validate: value => value !== '' })}/>
          </InputGroup>
        </FormGroup>
        <Button className='mr-1' color='primary'>
          Submit
        </Button>
        <Button color='secondary' onClick={handleModal} outline>
          Cancel
        </Button>
        </Form>
      </ModalBody>
    </Modal>
  )
}

export default AddNewModal
